import io from 'socket.io-client';
// redux
import { onRecvLogin, onRecvUsers, onRecvLogout } from 'public/store/actions/userAction';
import { addMessage, onRecvRoom, setRoomId } from 'public/store/actions/roomAction';
import { clearAll } from 'public/store';
export var socket = io('/chat');
socket.on('connect', function () {
    clearAll();
    console.log('socket.io connected');
});
socket.on('disconnect', function () {
    clearAll();
});
socket.on('login', function (user) {
    onRecvLogin(user);
});
socket.on('logout', function () {
    onRecvLogout();
});
socket.on('room', function (room) {
    onRecvRoom(room);
});
socket.on('room/in', function (roomId) {
    setRoomId(roomId);
});
socket.on('room/out', function (roomId) {
    setRoomId();
});
socket.on('room/message', function (msg) {
    addMessage(msg);
});
socket.on('users', function (users) {
    onRecvUsers(users);
});
export var reqUserLogin = function (name) {
    if (!socket.connected) {
        return false;
    }
    socket.emit('login', { name: name });
    return true;
};
export var reqUserLogout = function () {
    socket.emit('logout');
};
export var reqRooms = function (roomId) {
    if (!socket.connected) {
        return false;
    }
    socket.emit('room', roomId ? { id: roomId } : undefined);
    return true;
};
export var reqUsers = function () {
    if (!socket.connected) {
        return false;
    }
    socket.emit('users');
    return true;
};
export var reqAddRoom = function (name) {
    if (!socket.connected) {
        return false;
    }
    socket.emit('room/new', { name: name });
    return true;
};
export var reqEnterRoom = function (id) {
    if (!socket.connected) {
        return false;
    }
    socket.emit('room/in', { id: id });
    return true;
};
export var reqLeaveRoom = function (id) {
    if (!socket.connected) {
        return false;
    }
    socket.emit('room/out', { id: id });
    return true;
};
export var reqSendMessage = function (msg) {
    if (!socket.connected) {
        return false;
    }
    socket.emit('room/message', msg);
    return true;
};
export var reqInvite = function (msg) {
    if (!socket.connected) {
        return false;
    }
    socket.emit('room/invite', msg);
    return true;
};
export var logout = function () {
    socket.disconnect();
};
export default socket;
